@include font('martel-regular-webfont',martel);
@include font('martel-bold-webfont',martel-bold);
@include font('Rubik-Medium',rubik-medium);
@include font('Rubik-Regular',rubik-regular);


@mixin martel {
	font-family: martel, sans-serif; }

@mixin rubik-medium {
	font-family: rubik-medium, sans-serif; }

@mixin rubik-regular {
	font-family: rubik-regular, sans-serif; }

@mixin martel-bold {
	font-family: martel-bold, sans-serif; }

body {
	background: #FFF;
	@include css-lock(16, 50, 600, 1000);
	line-height: 1.4; }

