html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

@font-face {
  font-family: "martel-regular-webfont";
  src: url("../fonts/martel.woff2") format("woff2"), url("../fonts/martel.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "martel-bold-webfont";
  src: url("../fonts/martel-bold.woff2") format("woff2"), url("../fonts/martel-bold.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "Rubik-Medium";
  src: url("../fonts/rubik-medium.woff2") format("woff2"), url("../fonts/rubik-medium.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "Rubik-Regular";
  src: url("../fonts/rubik-regular.woff2") format("woff2"), url("../fonts/rubik-regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

body {
  background: #FFF;
  font-size: 50px;
  font-size: calc(16px + (50 - 16) * ((100vw - 600px) / (1000 - 600)));
  line-height: 1.4;
}

@media (max-width: 600px) {
  body {
    font-size: 16px;
  }
}

@media (min-width: 1000px) {
  body {
    font-size: 50px;
  }
}

.block {
  display: flex;
}

#map {
  height: 400px;
  height-width: 100%;
}

.wrapper {
  max-width: 1024px;
  margin: 0 auto;
  font-family: martel, sans-serif;
}

.container {
  margin: 0 auto;
  max-width: 725px;
  text-align: center;
  position: relative;
}

.container:after {
  content: " ";
  display: table;
  clear: both;
}

@media only screen and (max-width: 750px) {
  .container {
    margin: 0 20px;
  }
}

.header {
  margin: 0 auto;
}

.header .container {
  position: relative;
  max-width: 832px;
  width: 100%;
  height: 126px;
}

.header__logo, .header__nav {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.header__logo {
  float: left;
}

.header__nav {
  margin-left: 40%;
  margin-top: 7px;
  float: right;
}

.logo {
  font-size: 26px;
  text-decoration: none;
}

.logo__part1 {
  color: #0e1d2b;
}

.logo__part2 {
  color: #ffa326;
}

.nav {
  font-size: 16px;
}

.nav a {
  text-decoration: none;
  color: #0e1d2b;
  font-family: martel, sans-serif;
}

.hero {
  display: table;
  max-width: 1024px;
  width: 100%;
}

.hero__container {
  display: table-row;
}

.hero__left, .hero__right {
  height: 395px;
  display: table-cell;
  border: 0;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 100% 100%;
}

.left__bckg, .right__bckg {
  width: 100%;
  height: 100%;
  opacity: 0.92;
  padding: 15%;
  position: relative;
}

.left__bckg:after, .right__bckg:after {
  content: " ";
  display: table;
  clear: both;
}

.left__bckg {
  background-color: #011627;
}

.right__bckg {
  background-color: #ff9f1c;
}

.left__title, .right__title {
  font-size: 65px;
  line-height: 0.923;
  font-family: rubik-medium, sans-serif;
}

.left__title {
  color: #ffa326;
}

.right__title {
  color: #ffffff;
}

.left__text, .right__text {
  font-size: 14px;
  font-family: martel, sans-serif;
  line-height: 1.429;
  padding-top: 10%;
}

.left__text {
  color: #ffffff;
}

.right__text {
  color: #0d1e2d;
}

.showme {
  font-family: martel, sans-serif;
  font-size: 16px;
  background-color: #ffffff;
  width: 157px;
  height: 37px;
  box-shadow: 0px 3px 5px #444444;
  border: none;
}

.showme:hover {
  background-color: #dddddd;
}

.showme:active {
  transform: translateY(1px);
}

.top__photo {
  width: 100%;
}

.bottom__photo {
  width: 100%;
}

.burgers {
  margin: 72px auto;
}

.prices {
  width: 100%;
  box-sizing: border-box;
}

.price {
  display: inline-block;
  width: 22.34482758620%;
  border: 0;
}

.price:after {
  content: " ";
  display: table;
  clear: both;
}

.price__center {
  margin: 0 14.89%;
}

.price__title {
  font-size: 22.63px;
  font-family: rubik-regular, sans-serif;
  color: #f39c22;
  line-height: 0.692;
  margin: 7% 0;
}

.price__text {
  font-size: 12.18px;
  font-family: martel, sans-serif;
  color: #0b1d2c;
  line-height: 1.286;
  text-align: center;
}

.price__kj {
  margin: 0 4px;
  font-family: martel, sans-serif;
  font-size: 12.18px;
  color: #485058;
  line-height: 1.286;
  text-align: center;
}

.price__bucks {
  font-size: 20px;
  font-family: rubik-regular, sans-serif;
  color: #0b1d2c;
  line-height: 0.783;
  text-align: center;
}

.price__button {
  width: 100%;
  height: 30px;
  padding: 1%;
  background-color: #e1e1e1;
}

.whatelse {
  width: 100%;
  height: 404px;
  border: 0;
  margin: 0 auto;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 100% 100%;
  position: relative;
}

.whatelse:after {
  content: " ";
  display: table;
  clear: both;
}

.whatelse__background {
  height: 100%;
  width: 100%;
  opacity: 0.92;
  background: linear-gradient(#011627, #ff9f1c);
}

.whatelse .container {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 15%;
  text-align: center;
  max-width: 725px;
  width: 100%;
}

.whatelse__label, .whatelse__table {
  display: inline-block;
}

.whatelse__label {
  padding: 5% 0;
  float: left;
}

.whatelse__table {
  font-size: 16px;
  font-family: martel, sans-serif;
  color: #ffffff;
  line-height: 0.979;
  text-align: left;
  width: 288px;
  height: 226px;
  border: 3px solid #ff9f1c;
  float: right;
}

.whatelse__table table {
  margin-top: 50px;
  margin-left: 36px;
}

.whatelse__title {
  font-size: 60px;
  font-family: rubik-medium, sans-serif;
  color: #ffffff;
  line-height: 1;
}

.dish {
  width: 180px;
  padding-bottom: 15px;
}

.dish-price {
  max-width: 55px;
  padding-bottom: 15px;
}

.photos {
  margin-top: 65px;
}

.photos__title {
  font-size: 60px;
  font-family: rubik-medium, sans-serif;
  color: #0d1624;
  line-height: 1;
}

.photos__text {
  font-size: 12.18px;
  font-family: martel, sans-serif;
  color: #0b1d2c;
  line-height: 1.286;
  text-align: center;
}

.photos h1 {
  margin: 10px auto;
}

.photos__gallery {
  width: 100%;
  margin-top: 65px;
}

.photos__gallery img {
  max-width: 100%;
  transform: scale(1.01);
}

.photos__gallery img:hover {
  transform: scale(1.05);
}

.photos__gallery a {
  cursor: pointer;
}

.gallery__top, .gallery__bottom {
  margin: 0;
  padding: 0;
  line-height: 0.6;
  position: relative;
}

.gallery__top:after, .gallery__bottom:after {
  content: " ";
  display: table;
  clear: both;
}

.top__photo {
  width: 33.333333333333333333333333333333%;
  float: left;
  margin: 0;
}

.top__photo img {
  height: 184px;
}

.bottom__photo {
  width: 16.666666666666666666666666666667%;
  float: left;
  margin: 0;
  margin-top: -10px;
  z-index: 900;
  position: relative;
}

.bottom__photo img {
  height: 92px;
}

.board {
  position: absolute;
  top: -10px;
  left: 0;
  right: 0;
  width: 100.2%;
  margin-left: -1px;
  height: 10px;
  border: 3px solid #ff9f1c;
  box-sizing: border-box;
  z-index: 1000;
}
